.header,
.footer {
  color: #08070d; }

.bg-dark .header {
  background-color: #08070d; }

.bg-dark .footer {
  background-color: #4e4e56; }

.bg-light .header {
  background-color: #c8c7d1; }

.bg-light .footer {
  background-color: #807e8b; }

@media (max-width: 639px) {
  .header {
    padding-top: 6em;
    padding-bottom: 1em; } }

.header p {
  font-size: 1em; }
  @media (max-width: 639px) {
    .header p {
      font-size: .875em; } }

.bg-dark .header p,
.bg-dark .btn-text,
.bg-dark .footer,
.bg-dark .footer small,
.bg-dark .demo-tiles-header-text .number-shades,
.nav_list a,
.nav_toggle,
.bg-dark .nav-variation_list .current a,
.bg-light .nav-variation_list a,
.button {
  letter-spacing: .5px;
  word-spacing: -.5px; }

.footer {
  padding: 1.5em 0 2em; }

.bg-dark .header a,
.bg-dark .footer a {
  color: #5190f6;
  transition: all .3s; }
